import { Component, Inject, Injector, forwardRef, ElementRef, NgModule, ViewEncapsulation } from '@angular/core';
import { RouterModule, Router } from '@angular/router';
import { CommonModule } from '@angular/common';
import { Util } from '../../../utils';
import { OSharedModule } from '../../../shared';
import { OBarMenuComponent } from '../o-bar-menu.component';
import { OBaseMenuItemClass } from '../o-base-menu-item.class';

export const DEFAULT_INPUTS_O_BAR_MENU_ITEM = [
  ...OBaseMenuItemClass.DEFAULT_INPUTS_O_BASE_MENU_ITEM,
  // route [string]: name of the state to navigate. Default: no value.
  'route',

  // action [function]: function to execute. Default: no value.
  'action'
];

@Component({
  moduleId: module.id,
  selector: 'o-bar-menu-item',
  template: `
    <div class="fake-li mat-primary" *ngIf="!restricted" (click)="collapseMenu($event)" [class.fake-li-hover]="isHovered">
      <a *ngIf="action || route" class="mat-list-item" (click)="onClick()">
        <mat-icon class="o-bar-menu-item-icon mat-24">{{ icon }}</mat-icon>
        <div class="o-bar-menu-item-title">{{ title | oTranslate }}</div>
      </a>
      <div *ngIf="!route && !action" class="o-bar-menu-item-text mat-list-item">
        <mat-icon class="o-bar-menu-item-icon mat-24">{{ icon }}</mat-icon>
        <div class="o-bar-menu-item-title">{{ title | oTranslate }}</div>
      </div>
    </div>
  `,
  styles: [`
    .o-bar-menu-item[disabled=true] .fake-li{opacity:.5}.o-bar-menu-item[disabled=true] .fake-li a.mat-list-item{cursor:default}.o-bar-menu-item .o-bar-menu-item-text{display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-webkit-flex-direction:row;-ms-flex-direction:row;flex-direction:row;-webkit-box-align:center;-webkit-align-items:center;-ms-flex-align:center;align-items:center;box-sizing:border-box;font-size:16px;height:48px;padding:0 16px}.o-bar-menu-item .o-bar-menu-item-icon{display:inline-block;margin:6px 10px 0 0}.o-bar-menu-item .o-bar-menu-item-icon ~ .o-bar-menu-item-title{padding-left:0}.o-bar-menu-item .o-bar-menu-item-title{display:inline-block;position:relative;top:3px;padding-left:40px}
  `],
  inputs: DEFAULT_INPUTS_O_BAR_MENU_ITEM,
  encapsulation: ViewEncapsulation.None,
  host: {
    '[class.o-bar-menu-item]': 'true',
    '[attr.disabled]': 'disabled'
  }
})
export class OBarMenuItemComponent extends OBaseMenuItemClass {

  public static DEFAULT_INPUTS_O_BAR_MENU_ITEM = DEFAULT_INPUTS_O_BAR_MENU_ITEM;
  protected router: Router;
  route: string;
  action: Function;

  constructor(
    @Inject(forwardRef(() => OBarMenuComponent)) protected menu: OBarMenuComponent,
    protected elRef: ElementRef,
    protected injector: Injector
  ) {
    super(menu, elRef, injector);
    this.router = this.injector.get(Router);
  }

  ngOnInit() {
    // if (typeof (this.route) === 'string') {
    //   // TODO, permisos por route?
    // } else {
    //   this.restricted = false;
    // }
    super.ngOnInit();
  }

  collapseMenu(evt: Event) {
    if (this.menu) {
      this.menu.collapseAll();
    }
  }

  onClick() {
    if (this.disabled) {
      return;
    }
    if (Util.isDefined(this.route)) {
      this.router.navigate([this.route]);
    } else if (Util.isDefined(this.action)) {
      this.action();
    }
  }
}

@NgModule({
  declarations: [OBarMenuItemComponent],
  imports: [OSharedModule, CommonModule, RouterModule],
  exports: [OBarMenuItemComponent]
})
export class OBarMenuItemModule {
}
